h1{
  font-size: 34px;
  line-height: 1em;
  font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  padding: 0px;
  margin: 0px;
  padding-bottom: 12px;
}
h2{
  font-size: 29px;
  line-height: 1em;
  font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  padding: 0px;
  margin: 0px;
  padding-bottom: 12px;
}
h3{
  font-size: 21px;
  line-height: 1em;
  font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  padding: 0px;
  margin: 0px;
}
p{
  padding: 0px;
  margin: 0px;
}
.white-text{
  color:rgb(246, 245, 242);
}
.text-align-right{
  text-align: right;
  }
main{
  position:relative;
  height: 100%;
  width: 100%;
}
.content-top{
  top: 144px !important;
  }
.content{
  position: relative;
  display: block;
  width: 100%;
  min-height: 900px;
  margin: 0px auto;
  padding: 89px 21px;
  }
.main-image{
  position: relative;
  width: 80%;
  max-width: 1598px;
  margin: 0px auto;
  padding-bottom: 280px;
}

/* ---------- BOOTSTRAP ADJUSTMENTS ---------- */
.row{
  margin-left: -11px !important;
  margin-right: -11px !important;
}


@media only screen and (min-width:768px){
  .content{
      padding: 89px 55px;
      } 
  }
  @media only screen and (min-width:992px){
    h1{
      font-size: 55px;
    }
    h2{
      font-size: 47px;
    }
    h3{
      font-size: 34px;
    }
    .content{
      max-width: 843px;
      padding: 89px 0px;
      }
  }
  @media only screen and (min-width:1200px){
    
  .content{
      max-width: 987px;
      padding: 144px 0px;
      }   
  }
  @media only screen and (min-width:1400px){
    h1{
      font-size: 76px;
      line-height: 64px;
    }
    h2{
      font-size: 55px;
      line-height: 60px;
    }
    h3{
      font-size: 47px;
      line-height: 60px;
    }
    .content{
        max-width: 1220px;
        }    
    }
  @media only screen and (min-width:1550px){
  .content{
      max-width: 1364px;
      }
  }
  @media only screen and (min-width:1800px){
  .content{
      max-width: 1597px;
      }
  }
  