.scale-button{
    position: fixed;
    display: block;
    top: 0px;
    left: 0px;
    height: 30px;
    width: 30px;
    background-color: transparent;
    cursor: pointer;
    }
header{
    position: fixed;
    display: block;
    top: 0px;
    height: auto;
    padding-bottom: 20px;
    width: 100%;
    background-color: transparent;
    background-image: linear-gradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    z-index: 1000;
    }
header.bg-color-on{
    padding-bottom: 0px;
    background-color: rgb(246, 245, 242);
    background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0));
    }
header.dark{
    background-image: linear-gradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    background-color: transparent;
    }
header .header-container{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: top;
    width: 100%;
    max-width: 1597px;
    margin: 0px auto;
    padding: 18px 18px;
    }
/* ----- LOGO -----*/
header .header-container .header-logo{
    position:relative;
    display: flex;
    height: auto;
    width: 233px;
    margin-left: -19px;
    }


