    }

*/
header .header-container .header-logo .header-logo-text{
    position: absolute;
    display: block;
    top: 33px;
    left: 34px;
    font-family: 'HCo Gotham SSm Light';
    text-transform: uppercase;
    font-weight: normal;
    font-size: 10px;
    letter-spacing: 0.25em;
    text-decoration: none !important;
    color: rgb(246, 245, 242);
    }
header .header-container .header-logo .header-logo-text.light{
    color: rgb(100, 100, 100);
    }


/* ----- /LOGO -----*/
/* ----- DROP DOWN MENU BUTTON -----*/
.menu-drop-down-btn{
    position: relative;
    display: flex;
    right: 0px;
    margin-right: 0px;
    height: 21px;
    width: 34px;
    cursor: pointer;
    z-index: 1002;
    margin-top: 7px;
    }
.menu-drop-down-btn i{
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: rgb(246, 245, 242);
    transition: all ease-in-out 300ms;
    }
header.header .menu-drop-down-btn.light i{
    background-color: rgb(100, 100, 100);
    }
.menu-drop-down-btn i:nth-child(1){
    top: 0px;
    }
.menu-drop-down-btn i:nth-child(2){
    top: 50%;
    transform: translateY(-50%);
    }
.menu-drop-down-btn i:nth-child(3){
    bottom: 0px;
    }
.menu-drop-down-btn.close i:nth-child(1){
    transform: rotate(45deg);
    top: 9.5px;
    background-color: rgb(246, 245, 242);
    }
.menu-drop-down-btn.close i:nth-child(2){
    opacity:0;
    }
.menu-drop-down-btn.close i:nth-child(3){
    transform: rotate(-45deg);
    bottom: 9.5px;
    background-color: rgb(246, 245, 242);
    }
/* ----- /DROP DOWN MENU BUTTON -----*/
/* ----- DROP DOWN MENU ----- */
.drop-down-menu{
    position: fixed;
    left: 0px;
    right: 0px;
    display: block;
    height: 100%;
    width: 100%;
    background-color: transparent;
    z-index: 1001;
    top: -100%;
    transition: all ease-in-out 500ms;
    background-repeat: no-repeat;
    background-size: cover;
    /*background-attachment: fixed;*/
    background-image: url(../../images/menu-background-1.webp);
    background-color: #222;
    overflow: hidden;
    background-position: center center;
    }
.drop-down-menu::before{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    }
.drop-down-menu.background-1{
    background-image: url(../../images/menu-background-1.webp);
    background-position: center center;
    }
.drop-down-menu.background-2{
    background-image: url(../../images/menu-background-2.webp);
    background-position: center center;
    }
.drop-down-menu.background-3{
    background-image: url(../../images/menu-background-3.webp);
    background-position: center center;
    }
.drop-down-menu.background-4{
    background-image: url(../../images/menu-background-4.webp);
    background-position: center center;
    }
.drop-down-menu.on{
    top: 0%;
    }
.drop-down-menu .menu{
    position:relative;
    display: block;
    }
.drop-down-menu .menu ul{
    position:relative;
    display: block;
    top: 0px;
    left: 0px;
    padding: 76px 0px 0px 21px;
    }
.drop-down-menu .menu ul .nav-link{
    position: relative;
    display: block;
    color:rgb(246, 245, 242);
    font-size: 55px;
    line-height: 55px;
    font-family: bodoni-pt-variable, sans-serif;
    font-style: normal;
    font-variation-settings: "opsz" 60, "wght" 700;
    padding: 8px 0px;
    cursor: pointer;
    width: max-content;
    text-decoration: none;
    transition: all ease-in-out 500ms !important;
    pointer-events: none;
    }
.drop-down-menu.on .menu ul .nav-link{
    pointer-events: all;
    transition: all ease-in-out 500ms;
    }

.drop-down-menu.on .menu ul .nav-link::after{
    content: "";
    position: absolute;
    height: 6px;
    width: 0px;
    top: 63px;
    left: 0%;
    background-color: rgb(246, 245, 242);
    transition: all ease-in-out 500ms;
    }
.drop-down-menu .menu ul .nav-link:hover{
    transition: all ease-in-out 500ms;
    width: 100%;
    /*text-decoration: underline solid rgb(246, 245, 242);
    text-underline-offset: 6px;
    text-decoration-thickness: 5px;*/
    font-weight:normal ;
    }
.drop-down-menu .menu ul .nav-link:hover::after{
    width: 3000px;
    }
.drop-down-menu .menu ul .nav-link.active{
    /*text-decoration: underline solid rgb(246, 245, 242);
    text-underline-offset: 6px;
    text-decoration-thickness: 5px;*/
    font-weight:normal ;
    }
.drop-down-menu.background-1 .menu ul .nav-link.nav-1{
    /*text-decoration: underline solid rgb(246, 245, 242);
    text-underline-offset: 6px;
    text-decoration-thickness: 5px;*/
    }
.drop-down-menu.background-2 .menu ul .nav-link.nav-2{
    /*text-decoration: underline solid rgb(246, 245, 242);
    text-underline-offset: 6px;
    text-decoration-thickness: 5px;*/
    }
.drop-down-menu.background-3 .menu ul .nav-link.nav-3{
    /*text-decoration: underline solid rgb(246, 245, 242);
    text-underline-offset: 6px;
    text-decoration-thickness: 5px;*/
    }
.drop-down-menu.background-4 .menu ul .nav-link.nav-4{
    /*text-decoration: underline solid rgb(246, 245, 242);
    text-underline-offset: 6px;
    text-decoration-thickness: 5px;*/
    }

