/* ----- WORK MENU ----- */
.work-menu{
    position: absolute;
    top: 58px;
    right: 112px;
    display: none;
    width: 0px;
    white-space: nowrap !important;
    }
.work-menu.on{
    display: block;
    }
.work-menu .work-menu-link{
    display: block;
    position: relative;
    font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
    color: #ccc !important;
    text-decoration: none !important;
    font-size: 21px;
    padding: 0px 18px 0px 0px;
    line-height: 29px;
    transition: all ease-in-out 200ms;
    }
.work-menu-link:hover{
    /*font-size: 29px;*/
    color: #999 !important;
    }
.work-menu-link.active{
    /*font-size: 29px; */
    color: #999 !important;
    }

.menu-label{
    position: absolute;
    display: block;
    top: 30px;
    text-align: right;
    right: 46px;
    font-size: 29px !important;
    font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
    color: #777 !important;
    line-height: 1em;
    }
.menu-label.on{
    display: block;
}
