/* ----- SUB MENU WORK ----- */
.sub-menu{
    display: none;
    position: relative;
    /*padding: 11px 0px;*/
    text-align: right;
    height: 0px;
    opacity: 0;
    transition: all ease-in-out 500ms;
    }
.sub-menu.on{
    display: block;
    opacity: 1;
    transition: all ease-in-out 500ms;
    }
.sub-nav-link{
    display: inline-block;
    position: relative;
    color: rgb(246, 245, 242) !important;
    text-decoration: none;
    font-size: 21px;
    padding: 0px 18px 0px 0px;
    text-decoration: none;
    text-shadow: 0px 0px 30px rgba(0, 0, 0, 1), 0px 0px 60px rgba(0, 0, 0, 1);
    }
    
.sub-nav-link.active, .sub-nav-link:hover{
